.login-child,
.login1 {
  position: relative;
}
.login-child {
  border-top: 5px solid #1f59b2;
  box-sizing: border-box;
  width: 79px;
  height: 5px;
}
.usurio {
  border: 0;
  font-weight: 500;
  font-family: var(--font-inter);
  font-size: var(--font-size-16xl);
  background-color: transparent;
  position: relative;
  color: var(--color-gray);
  text-align: left;
}
.login-item {
  position: relative;
  width: 348.01px;
  height: 3.06px;
}
.esqueceu-a-senha {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: 20px;
  display: inline-block;
  font-style: italic;
  font-weight: 300;
  font-family: var(--font-inter);
  color: var(--color-cornflowerblue);
  text-align: left;
}
.botao-child {
  align-self: stretch;
  position: relative;
  border-radius: 70px;
  background-color: var(--color-cornflowerblue);
  height: 70px;
}
.acessar {
  position: relative;
  font-size: 42px;
  font-weight: 500;
  font-family: var(--font-inter);
  color: #e7e7e7;
  text-align: left;
  margin-top: -61px;
}
.botao,
.login {
  display: flex;
  flex-direction: column;
}
.botao {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  width: 329px;
  align-items: center;
  justify-content: center;
}
.login {
  position: relative;
  background-color: #ebeffa;
  width: 100%;
  overflow: hidden;
  padding: 119px 73px;
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 34px;
  text-align: left;
  font-size: 50px;
  color: #444;
  font-family: var(--font-inter);
}
